</template>

<script>
export default {
  name: "Post",
  data(){
    return{
      PostId:'',
      Title:'银龙重铸之日，骑士归来之时',
      Content:'#英雄联盟手游来了#国服开发者日志第二期划重点！\n' +
          '              开服福利非常丰富，可以免费获得十几款皮肤、几十个英雄以及各种道具奖励。\n' +
          '              1、无限热爱版“熊猫提莫”皮肤+提莫英雄\n' +
          '              2、完成任务可以免费获得福牛守护者全系列6款皮肤\n' +
          '              3、升到十级可以获得内瑟斯、阿狸、艾希、安妮、布里茨、易大师、盖伦、迦娜、金克丝、拉克丝、蔚共计11个英雄\n' +
