          '              5、赏金计划登陆签到可以获得厄运小姐英雄以及“西部牛仔 厄运小姐”\n' +
          '              6、峡谷之旅活动可以获得3个英雄自选宝箱、一个永久皮肤5选1宝箱和一个永久皮肤自选宝箱\n' +
          '              7、冰与火活动可以免费获得“努努”和“布兰德”英雄\n' +
