<template>
  <el-container @load.native="created">
    <el-aside width="200px">
      <el-menu @select="onMenuOptionSelected"
               default-active="综合"
               class="el-menu-vertical-demo"
               @open="handleOpen"
               @close="handleClose">
        <el-menu-item index="综合">
          <i class="el-icon-location"></i>
          <span>综合</span>
        </el-menu-item>

        <el-menu-item index="时事">
          <i class="el-icon-menu"></i>
          <span slot="title">时事</span>
        </el-menu-item>

        <el-menu-item index="校园">
          <i class="el-icon-document"></i>
          <span slot="title">校园</span>
        </el-menu-item>

        <el-menu-item index="故事">
          <i class="el-icon-setting"></i>
          <span slot="title">故事</span>
        </el-menu-item>

        <el-menu-item index="体育">
          <i class="el-icon-setting"></i>
          <span slot="title">体育</span>
        </el-menu-item>

        <el-menu-item index="影视">
          <i class="el-icon-setting"></i>
          <span slot="title">影视</span>
        </el-menu-item>

        <el-menu-item index="游戏">
          <i class="el-icon-setting"></i>
          <span slot="title">游戏</span>
        </el-menu-item>

        <el-menu-item index="动漫">
          <i class="el-icon-setting"></i>
          <span slot="title">动漫</span>
        </el-menu-item>

        <el-menu-item index="同城">
          <i class="el-icon-setting"></i>
          <span slot="title">同城</span>
        </el-menu-item>

      </el-menu>
    </el-aside>
    <el-container>
      <el-main>
        <el-row  id="postsDisplayArea" v-for="post in PostResults" v-bind:key="post" @click.native="getDetail(post)">
          <el-col >
            <el-row :span = "4" style="text-align: left">
              {{post.uid}}&emsp;{{post.DT}}
            </el-row>
            <el-row :span = "8" style="font-size: larger; color: black;line-height: initial">
              {{post.title}}
            </el-row>
            <el-row :span = "12" style="line-height: initial;text-align: left" >
              &emsp;&emsp;{{post.content}}
            </el-row>
            <el-divider style="margin: 10px"></el-divider>
          </el-col>

        </el-row>

      </el-main>
      <el-footer>
        <el-pagination
            background
            layout="prev, pager, next"
            page-count="5"
            :total="this.TotalPages*5"
            :current-page="this.CurrentPage"
            @current-change="changePage">
        </el-pagination>
      </el-footer>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: "Parts",
  data(){
    return{
      CurrentTag:'综合',
      CurrentPage : 1,
      TotalPages: 10,
      BaseUrl:'https://localhost:8001/',
      PostResults: [
        {
          pid:"1",
          DT : "1970-1-1 11:11",
          uid : "jack",
          title: "关于一些问题",
          content:"\n" +
              "<div id=\"app\">\n" +
              "  <ul>\n" +
              "    <li v-for=\"(value, key) in object\">\n" +
              "    {{ key }} : {{ value }}\n" +
              "    </li>\n" +
              "  </ul>\n" +
              "</div>\n"
        },
        {
          pid:"2",
          DT : "1970-1-1 11:11",
          uid : "jack",
          title: "关于一些问题",
          content:"AirPods Max支持强大的主动降噪能力，两个耳罩内都有Apple H1芯片，内置10个音频核心，加上特制的声学设计、先进的软件，以及计算音频技术，可有效阻隔外部噪音，营造临场感，轻按噪声控制按钮还可以切换至通透模式。\n" +
              "\n" +
              "它还配备总共多达九个麦克风，其中八个用于主动降噪，包括六个外向式、两个内向式，三个用于语音拾取，包括一个专用、两个兼用主动降噪。\n" +
              "\n" +
              "在打电话时，波束成形麦克风还有助于将语音从背景噪音中分离出来。\n" +
              "\n" +
              "动圈式驱动单元可实现宽广的频率范围，双环形钕磁体电机驱动单元则能在整个可听范围内实现极低的总谐波失真。"

              +"耳机自带数码旋钮，可以精确调节音量、跳播曲目、接听/暂停电话、激活Siri。\n" +
              "\n" +
              "此外还支持自适应均衡、持动态头部追踪空间音频、头部检测、信息播报等特色功能。"
        },
        {
          pid:"3",
          DT : "1970-1-1 11:11",
          uid : "jack",
          title: "关于一些问题",
          content:"提到新能源汽车，很多人首先想到的就是特斯拉。在Model 3国产后，月销量破万的数据似乎更是加强了特斯拉的“神话”。当特斯拉还在主打Model 3的时候，国产电动汽车元老——比亚迪再推出汉EV。并且以27万+的超低价格为消费者奉上了“高性能版”。那它到底够高性能么？\n" +
              "为了验证汉EV高性能版的综合产品力，我们将从性能、舒适便利性、能耗、充电、驾驶体验、空间等多个角度对它进行全面评测。同时在直线加速、赛道圈速和电池管理这三项测试上，我们还请来了特斯拉Model 3长续航版作为标杆，给大家做个参考：比亚迪汉EV PK 特斯拉Model 3 是针尖对麦芒？还是实力悬殊？"
        }
      ]
    }
  },
  created(){
    this.getPostByPages(5,1)
    console.log("mounted")
  },
  methods: {
    getPostByPages(length,number){
      this.$http.get(this.BaseUrl+'post/allposts/',{
        params: {
          length : length,
          number : number
        }
      }).then(response => {
        console.log(response)
        this.PostResults = response.data
        this.PostResults.forEach(m => {
          m.content.replaceAll('\n','</br>')
        })
      }).error(e => {
        console.log(e)
      })
    },
    getDetail(post){
      console.log("clicked")
      this.$router.push('/main/posts/'+post.pid)
    },
    changePage(page){
      this.getPostByPages(page)
    }
  }
}
</script>

<style scoped>
.el-aside{
  background: rgba(255, 236, 140, 0.14);
}
.el-main{
  height: 500px;
}
</style>
