<script>
export default {
  name: 'login',
  data () {
    return {
      UserId: '',
      Password: '',
      BaseUrl: 'http://localhost:5001'
    }
  },
  methods: {
    // created:function (){
    //   console.log("mounted")
    //   this.logon()
    // },
    // mounted: function(){
    //   console.log("created")
    //   this.logon()
    // },
    login: function () {
      //this.$router.push('/main')
      this.$http.get( '/login/authorize/user',
          {
            params: {
              id : this.UserId,
              pwd : this.Password
            }
      }
        ).then(response => {
              console.log(response)
              this.$router.push('/main')
        }).catch(error => {
              console.log(error)
        })
    },
    logon: function (){
      var  param = new URLSearchParams()
      param.append("UserName", this.UserId)
      param.append("Password", this.Password)
      this.$http.post('/login/register',
          {
              UserName : this.UserId,
              Password : this.Password
          }
      ).then(response => {
        console.log(response)
        this.$router.push('/main')
      }).catch(error => {
        console.log(error)
      })
    }
  }
}
</script>

